import * as Yup from 'yup';

export const emailLoginSchema = Yup.object({
  email: Yup.string()
    .email('Geçerli bir e-posta girin')
    .required('E-posta zorunludur'),
  password: Yup.string()
    .required('Şifre zorunludur')
    .min(6, 'Şifre en az 6 karakter olmalı'),
});

export const mobileLoginSchema = Yup.object({
  phoneNumber: Yup.string()
    .required('Telefon numarası zorunludur')
    .min(10, 'Telefon numarası en az 10 karakter olmalı'),
  password: Yup.string()
    .required('Şifre zorunludur')
    .min(6, 'Şifre en az 6 karakter olmalı'),
});

export const emailRegisterSchema = Yup.object({
  name: Yup.string()
    .required('İsim zorunludur')
    .min(2, 'İsim en az 2 karakter olmalı'),
  nickname: Yup.string()
    .required('Kullanıcı adı zorunludur'),
  country: Yup.string()
    .required('Ülke zorunludur'),
  uidCode: Yup.string()
    .required('UID kodu zorunludur'),
  email: Yup.string()
    .email('Geçerli bir e-posta girin')
    .required('E-posta zorunludur'),
  password: Yup.string()
    .required('Şifre zorunludur')
    .min(6, 'Şifre en az 6 karakter olmalı'),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('password')], 'Şifreler eşleşmiyor')
    .required('Şifre tekrarı zorunludur'),
});

export const mobileRegisterSchema = Yup.object({
  name: Yup.string()
    .required('İsim zorunludur')
    .min(2, 'İsim en az 2 karakter olmalı'),
  nickname: Yup.string()
    .required('Kullanıcı adı zorunludur'),
  country: Yup.string()
    .required('Ülke zorunludur'),
  uidCode: Yup.string()
    .required('UID kodu zorunludur'),
  phoneNumber: Yup.string()
    .required('Telefon numarası zorunludur')
    .min(10, 'Telefon numarası en az 10 karakter olmalı'),
  countryCode: Yup.string()
    .required('Ülke kodu zorunludur'),
  password: Yup.string()
    .required('Şifre zorunludur')
    .min(6, 'Şifre en az 6 karakter olmalı'),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('password')], 'Şifreler eşleşmiyor')
    .required('Şifre tekrarı zorunludur'),
});

export const forgotPasswordSchema = Yup.object({
  email: Yup.string()
    .email('Geçerli bir e-posta girin')
    .required('E-posta zorunludur'),
});
